import React from "react";
import streets from "./minsk.json";
import Autocomplete from "@material-ui/lab/Autocomplete";
import TextField from "@material-ui/core/TextField";
import List from "@material-ui/core/List";
import ListItem from "@material-ui/core/ListItem";
import Container from "@material-ui/core/Container";
import Typography from "@material-ui/core/Typography";
import Paper from "@material-ui/core/Paper";
import "./App.css";

function App() {
  const [value, setValue] = React.useState("");

  return (
    <div className="App">
      <Container
        maxWidth="sm"
        style={{ flexGrow: 1, display: "flex", flexDirection: "column" }}
      >
        <Paper style={{ flexGrow: 1, padding: "1em" }}>
          <Autocomplete
            onChange={(event, newValue) => {
              setValue(newValue && newValue.district);
            }}
            id="combo-box"
            options={streets}
            clearOnBlur
            getOptionLabel={(option) => option.street}
            renderInput={(params) => (
              <TextField {...params} label="Улица" variant="outlined" />
            )}
          />
          <List style={{fontSize: "1.5em"}}>
            <Typography align="center" style={{paddingTop: "1em"}}>Район:</Typography>
            {value &&
              value.split("\n").map((v, key) => {
                return <ListItem key={key}>{v}</ListItem>;
              })}
          </List>
        </Paper>
      </Container>
    </div>
  );
}

export default App;
